import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpClientModule } from '@angular/common/http';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { MyApp } from './app.component';

import { AddArticlePage } from '../pages/add-article/add-article';
import { ArticlesPage } from '../pages/articles/articles';
import { SearchPage } from '../pages/search/search';
import { TabsPage } from '../pages/tabs/tabs';

import { SearchTagsContainerComponent } from '../components/SearchTagsContainer/search-tags-container.component';
import { ArticleComponent } from '../components/Article/article.component';
import { ArticleTagsContainerComponent } from '../components/ArticleTagsContainer/article-tags-container.component';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { ArticlesService } from '../services/articles.service';

let declareAndEntry = [
  MyApp,
  AddArticlePage,
  ArticlesPage,
  SearchPage,
  TabsPage
];

@NgModule({
  declarations: [
    ...declareAndEntry,
    ArticleComponent,
    ArticleTagsContainerComponent,
    SearchTagsContainerComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    IonicModule.forRoot(MyApp)
  ],
  bootstrap: [IonicApp],
  entryComponents: [...declareAndEntry],
  providers: [
    ArticlesService,
    StatusBar,
    SplashScreen,
    InAppBrowser,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



