import { Component } from '@angular/core';
import {AlertController, LoadingController, NavController} from 'ionic-angular';
import { ArticlesService } from '../../services/articles.service';
import { Article } from '../../classes/Article';

@Component({
  selector: 'page-search',
  templateUrl: 'search.html'
})
export class SearchPage {

  public searchTag;

  public debug;

  public tagList: string[] = [];

  public articleList: Article[] = [];

  private loader;

  constructor(
    public navCtrl: NavController,
    public articlesService: ArticlesService,
    public alertCtrl: AlertController,
    public loadingCtrl: LoadingController
  ) {}

  public addTag( tag:string ){

    if( tag ) {

      this.tagList.push( tag );

    }

    this.getArticlesByTags( this.tagList );

    this.searchTag = null;

  }

  public getArticlesByTags( tags: string[] ): void {

    if( tags.length === 0 ){

      this.populateArticles( [] );

      return;

    }

    this.loader = this.loadingCtrl.create({
      content: 'Getting articles...'
    });
    this.loader.present();

    this.articlesService.getByTags( tags )
      .subscribe(
        ( data ) => this.getArticlesByTagsNext( data ),
        ( err ) => this.getArticlesByTagsError( err ),
        () => this.loader.dismiss()
      );

  }

  private getArticlesByTagsNext( data: any ): void {

    this.populateArticles( data );

  }

  private getArticlesByTagsError( err ): void {

    let alert = this.alertCtrl.create({
      title: 'There was a problem getting the articles :(',
      buttons: ['OK']
    });
    alert.present();

  }

  private populateArticles( articles: Article[] ): void {

    let _articles: Article[] = [];

    let _article: Article;

    for ( let article of articles ){

      _article = new Article( article.url, article.description, article.tags );

      _articles.push( _article );

    }

    this.articleList = _articles;

  }

  public removeTagFromList( tag: string ): void {

    let index: number = this.tagList.indexOf( tag );

    if( index === -1 ){

      console.log('how!')

    } else {

      this.tagList.splice( index, 1 );

    }

    this.getArticlesByTags( this.tagList );

  }

}



