import { Injectable }               from '@angular/core';
import { HttpClient, HttpHeaders }  from '@angular/common/http';
import 'rxjs/Rx';
import 'rxjs/add/observable/throw';
import { Observable }               from 'rxjs/Observable';
import { Article } from '../classes/Article';

import { Config } from '../config';

@Injectable()
export class ArticlesService {

  private headers: HttpHeaders = new HttpHeaders({'Content-Type': 'application/json'});

  private url: string = Config.HOST + ':' + Config.PORT + Config.APIPATH + '/article';

  constructor( private http: HttpClient ) {  }

  public getByTags( tags: string[] ): Observable<any> {

    return this.http.get(
      this.url,
      {
        headers: this.headers,
        params: { tag: tags }
      }
    );

  }

  public getAllArticles(): Observable<any> {

    return this.http.get(this.url);

  }

  public addArticle( article: Article ): Observable<any> {

    return this.http.post( this.url, article.toJson(), { headers: this.headers } )

  }

}



