import {Component, EventEmitter, Input, Output} from '@angular/core';


@Component({
  selector: 'search-tags-container',
  templateUrl: './search-tags-container.component.html'
})
export class SearchTagsContainerComponent {

  @Input()
  public searchTags: string[];

  @Output() public removeTagEmitter: EventEmitter<string> = new EventEmitter<string>();

  constructor() {}

  public removeTagClicked( tag: string ): void {

    this.removeTagEmitter.next( tag );

  }

}



