import {Component, OnInit} from '@angular/core';
import {AlertController, LoadingController, NavController} from 'ionic-angular';
import { ArticlesService } from '../../services/articles.service';
import { Article } from '../../classes/Article';

@Component({
  selector: 'page-articles',
  templateUrl: 'articles.html'
})
export class ArticlesPage implements OnInit {

  public articleList: Article[] = [];

  private loader;

  constructor(
    public navCtrl: NavController,
    public articlesService: ArticlesService,
    public alertCtrl: AlertController,
    public loadingCtrl: LoadingController
  ) {

  }

  ngOnInit() {

    this.getAllArticles();

  }

  private getAllArticles(): void {

    this.loader = this.loadingCtrl.create({
      content: 'Getting articles...'
    });
    this.loader.present();

    this.articlesService.getAllArticles()
      .subscribe(
        ( data ) => this.getAllArticlesNext( data ),
        ( err ) => this.getAllArticlesError( err ),
        () => this.loader.dismiss()
        );

  }

  private getAllArticlesNext( data ): void {

    this.articleList = data;

  }

  private getAllArticlesError( err ): void {

    let alert = this.alertCtrl.create({
      title: 'There was a problem getting the articles :(',
      buttons: ['OK']
    });
    alert.present();

  }

}



