import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { ArticlesService } from '../../services/articles.service';
import { Article } from '../../classes/Article';
import { AlertController } from 'ionic-angular';
import { LoadingController } from 'ionic-angular';


@Component({
  selector: 'page-add-article',
  templateUrl: 'add-article.html'
})
export class AddArticlePage {

  public article = {};

  public loader;

  constructor(
    public navCtrl: NavController,
    public articlesService: ArticlesService,
    public alertCtrl: AlertController,
    public loadingCtrl: LoadingController ) {

  }

  public formSubmit() {

    let tags = this.article['tags'];

    let tagsList = tags.split(',');

    let tag;

    for( let i=0; i< tagsList.length; i++ ){

      tag = tagsList[i].trim();

      if ( tag.length === 0 ){

        tagsList.splice( i, 1 );

      } else {

        tagsList[i] = tag;

      }

    }

    let _article: Article = new Article(this.article['url'], this.article['description'], tagsList);

    this.addArticle( _article );

  }

  private addArticle( article: Article ): void {

    this.loader = this.loadingCtrl.create({
      content: 'Adding article...'
    });
    this.loader.present();

    this.articlesService.addArticle( article )
      .subscribe(
        ( data ) => this.addArticleNext( data ),
        ( err ) => this.addArticleError( err ),
        () => this.loader.dismiss()
      );

  }

  private addArticleNext( data ): void {

    let alert = this.alertCtrl.create({
      title: 'Article Added!',
      buttons: ['OK']
    });
    alert.present();

    this.article = {};

  }

  private addArticleError( err ): void {

    let alert = this.alertCtrl.create({
      title: 'There was a problem adding the article :(',
      buttons: ['OK']
    });
    alert.present();

  }

  public tagsKeyUp( event: KeyboardEvent ): void {

    if ( event ) {

      if ( event.key === ',' ){

        console.log('comma')

      }

    }

  }

}



