import {Component, EventEmitter, Input, Output} from '@angular/core';


@Component({
  selector: 'article-tags-container',
  templateUrl: './article-tags-container.component.html'
})
export class ArticleTagsContainerComponent {

  @Input()
  public articleTags: string[];

  @Output() public tagEmitter: EventEmitter<string> = new EventEmitter<string>();

  constructor() {}

  public tagClicked( tag: string ): void {

    this.tagEmitter.next( tag );

  }

}



